      <div class="yes">
          <i class="iconfont icon-round-tick"></i>
          <span></span>
      </div>
      <div class="button-wrap-new">
        <button class="submit-btn-1" @click="submit"></button>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            fil: null,
            form: {
                cardNoImgs: [], // 
                accidentImgs: [], // 
                emergencyImgs: [], // 
                costImgs: [], // 
                prescriptionImgs: [], // 
                outpatientDepartmentImgs: [], // 
                bankCardImgs: [], // 
                inOutHospitalImgs: [], // 
                outCostListImgs: [], // 
                caseReportImgs: [], // 
                turnHospitalImgs: [] // 
            },
            query: {}
        };
    },
    created () {
        this.query = this.$route.query;
    },
    methods: {
        showImg (url) {
            this.$mint.MessageBox({
                title: '',
                message: `<img src="${url}">`
            });
        },
        uploadImg (type, list) {
            let inputDOM;
            if (type === 1) {
                inputDOM = this.$refs.inputer1;
            }
            if (type === 2) {
                inputDOM = this.$refs.inputer2;
            }
            if (type === 3) {
                inputDOM = this.$refs.inputer3;
            }
            if (type === 4) {
                inputDOM = this.$refs.inputer4;
            }
            if (type === 5) {
                inputDOM = this.$refs.inputer5;
            }
            if (type === 6) {
                inputDOM = this.$refs.inputer6;
            }
            if (type === 7) {
                inputDOM = this.$refs.inputer7;
            }
            if (type === 8) {
                inputDOM = this.$refs.inputer8;
            }
            if (type === 9) {
                inputDOM = this.$refs.inputer9;
            }
            if (type === 10) {
                inputDOM = this.$refs.inputer10;
            }
            if (type === 11) {
                inputDOM = this.$refs.inputer11;
            }
            // DOM
            this.fil = inputDOM.files;
            let formData = new FormData();
            let headers = {
                'Content-Type': 'multipart/form-data'
            };
            formData.append('file', this.fil[0]);
            formData.append('storage', 'platform-mgmt');
            formData.append('fileName', this.fil[0].name);
            this.$http.post('http://news.shcvs.cn/cm/upload/uploadImgFolderOSS?folderName=insurance', formData, headers).then(res => {
                if (res.data.errorcode === 0) {
                    list.push(res.data.data);
                }
            }).catch((e) => {
                alert(e);
            });
        },
        dele (idx, list) {
            list.splice(idx, 1);
        },
        submit () {
            if (!this.check()) return false;
            let params = {
                cardNoImgs: '',
                accidentImgs: '',
                emergencyImgs: '',
                costImgs: '',
                prescriptionImgs: '',
                outpatientDepartmentImgs: '',
                bankCardImgs: '',
                inOutHospitalImgs: '',
                outCostListImgs: '',
                caseReportImgs: '',
                turnHospitalImgs: ''
            };
            for (let i in this.form) {
                params[i] = this.form[i].join(';');
            }
            Object.assign(this.query, params);
            this.$http.post(this.$api.insurance.insurancePersonClaim, this.query).then(res => {
                if (res.data.code === 0) {
                    this.$router.push({name: 'result'});
                }
            });
        },
        check () {
            if (this.form.cardNoImgs.length < 2) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.accidentImgs.length === 0) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.emergencyImgs.length === 0) {
                this.$mint.Toast({
                    message: '/',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.costImgs.length === 0) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.bankCardImgs.length < 2) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            return true;
        }
    }
};
</script>
<style lang="scss" scoped>
  .insure-upload {
    .inputfile {
      position:absolute;
      clip:rect(0 0 0 0);
    }
    li {
      .block {
        margin-top: px2rem(15);
        .img-box {
          position: relative;
          display: inline-block;
          img {
            width: px2rem(60);
            height: px2rem(60);
            display: inline-block;
            margin-right: 5px;
            margin-bottom: 15px;
          }
          i {
            position: absolute;
            top: -2px;
            right: 7px;
          }
          .icon-close-blod:before {
            color: red;
          }
        }
      }
    }
  }
</style>



