<template>
  <section class="claims-on-line">
      <div class="lc-tips">
        <span>-</span>
      </div>
      <ul>
          <li class="">
               
          </li>
          <li class="">
               
          </li>
          <li class="">
               //
          </li>
          <li class="">
               
          </li>
          <li class="">
               
          </li>
          <li class="">
               
          </li>
          <li class="">
               
          </li>
          <li class="">
               
          </li>
          <li class="">
               
          </li>
      </ul>
      <div class="text"></div>
      <div class="btn-wrap-new">
        <button class="submit-btn-1" @click="jump"></button>
      </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            isShow: true,
            activation: null
        };
    },
    created () {
    },
    methods: {
        jump () {
            // claimStatus -10 10
            if (Number(this.$route.query.claimStatus) === -10) {
                this.$router.push({name: 'information', query: this.$route.query});
            } else {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
            }
        }
    }
};
</script>
<style lang="scss" scoped>
  .text {
    margin-top: 20px;
    padding-left: 0.65rem;
  }
</style>



