<template>
  <div class="body">
    <div class="box">
        <section class="noticeInsure claimExplain">
            <section class="entirety">
                <i class="i iconfont icon-jay"></i>
                <div class="claimText">
                    ,
                    
                    ,
                    
                </div>
            </section>
            <div class="introBtn">
                <button class="submit-btn-1" @click="jump"></button>
            </div>
        </section>
    </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    created () {
        if (this.$route.query.type === '1') {
            this.$route.meta.title = '';
        }
    },
    methods: {
        jump () {
            this.$router.go(-1);
        }
    }
};
</script>



