<template>
    <section class="insuranceIntro">
        <!--  -->
        <img class="head-img" :src="form.imgs" alt="">
        <div class="safePlan">
            <div class="row J">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span></span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r" @click="jump(1)">
                  <i class="iconfont icon-arrow-right"></i>
                </span>
            </div>
            <div class="listL">
                <ul>
                    <li class="row" v-for="(item, index) in list" :key="index">
                        <div class="col-sm col-sm-6 Tl">{{item.name}}</div>
                        <div class="col-sm col-sm-6 Tr">{{item.amount | amount}}</div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl"></div>
                        <div class="col-sm col-sm-6 Tr">{{form.insuranceMinAge}}-{{form.insuranceMaxAge}}
                          <span v-if="form.reInsuranceAge !== 0">({{form.reInsuranceAge}})</span>
                          <span v-else>()</span>
                          </div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl"></div>
                        <div class="col-sm col-sm-6 Tr">
                          <span v-if="form.validityPeriod === 0"></span>
                          <span v-else>{{form.validityPeriod}}</span>
                        </div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl"></div>
                        <div class="col-sm col-sm-6 Tr">{{form.insuranceAmount}}</div>
                    </li>
                </ul>
            </div>
        </div>

        <div class="safePlan">
            <div class="row J">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span></span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r" @click="jump(2)"><i class="iconfont icon-arrow-right"></i></span>
            </div>
            <div class="listL">
                <ul>
                    <li class="row">
                        <div class=" Tl"><a :href="'tel:' + form.claimMobile"></a></div>
                    </li>
                    <li class="row">
                        <div class=" Tl"></div>
                    </li>
                    <li class="row">
                        <div class=" Tl"></div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="safePlan">
            <div class="row J" @click="goto">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span></span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r">
                  <i class="iconfont icon-jay"></i>
                  <em class="jay">{{form.insuranceAmount}}/</em>
                </span>
            </div>
            <div class="listL">
                <ul>
                    <!-- <li class="row">
                        <div class="col-sm col-sm-6 Tl"></div>
                        <div class="col-sm col-sm-6 Tr">
                          
                          <i class="iconfont icon-arrow-right"></i>
                        </div>
                    </li> -->
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl"></div>
                        <div class="col-sm col-sm-6 Tr">{{form.freeAmount}}</div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="introBtn">
            <button class="submit-btn-1" @click="submit"></button>
        </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            form: {},
            list: [],
            id: null,
            num: null,
            prjvOrgId: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            // 
            let storage = localStorage.getItem('appUserInfo');
            if (storage) {
                storage = JSON.parse(storage);
                this.prjvOrgId = storage.prjvOrgId;
            }
            this.id = this.$route.query.id;
            this.$http.post(this.$api.insurance.queryInsuranceProductDetailById, {insuranceProductId: this.id, prjvOrgId: this.prjvOrgId}).then(res => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                    this.list = res.data.data.list;
                }
            });
        },
        goto () {
            this.$router.push({name: 'noticeInsure', query: {type: '1'}});
        },
        jump (type) {
            type === 1 ? this.$router.push({name: 'planDetails', query: this.form}) : this.$router.push({name: 'noticeInsure'});
        },
        submit () {
            if (this.form.freeAmount < this.form.insuranceAmount) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return;
            }
            this.$route.query['insuranceProductId'] = this.form.id;
            this.$router.push({name: 'selectUser', query: this.$route.query});
            // console.log(this.$route.query);
        }
    }
};
</script>
<style lang="scss" scoped>
  .head-img {
    width: 100%;
    height: px2rem(100);
    min-height: px2rem(100);
  }
  .jay {
    vertical-align: middle;
    margin-left: px2rem(-5);
  }
  .listL {
    a {
      text-decoration: none;
      color: #000;
    }
  }
</style>



