export default {
    minute2hour: function (time) {
        let hour;
        hour = (time % 60 === 0) ? time / 60 : (time / 60).toFixed(1);
        return hour;
    },
    cutstring: function (str, len) {
        let newstr;
        newstr = str.length > len ? (str.substring(0, len) + ' ...') : str;
        return newstr;
    },
    // -101020
    status: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '';
        }
        if (val === 20) {
            return '';
        }
        if (val === -10) {
            return '';
        }
        if (val === -20) {
            return '';
        }
        return '';
    },
    // -101020
    policyStatus: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '';
        }
        if (val === 20) {
            return '';
        }
        if (val === -10) {
            return '';
        }
        if (val === -20) {
            return '';
        }
        return '';
    },
    // -20 -10 10 20
    queryStatus: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '';
        }
        if (val === 20) {
            return '';
        }
        if (val === -10) {
            return '';
        }
        if (val === -20) {
            return '';
        }
        return '';
    },
    // 102030
    from: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '';
        }
        if (val === 20) {
            return '';
        }
        if (val === 30) {
            return '';
        }
        return '';
    },
    date: function (time, hour) { // :--::  --
        if (!time) {
            return '';
        }
        let y, m, d, date, h, ms, s, data, hours;
        date = new Date(time);
        y = date.getFullYear();
        m = date.getMonth() + 1;
        d = date.getDate();
        h = date.getHours();
        ms = date.getMinutes();
        s = date.getSeconds();
        data = y + '' + format(m) + '' + format(d) + '';
        hours = format(h) + ':' + format(ms) + ':' + format(s);
        if (hour === 'b') {
            return data.substring(5, 11);
        }
        if (hour) {
            if (hour === 'd') {
                return (data + ' ' + hours).substring(11, 17);
            }
            return data + ' ' + hours;
        } else {
            return data;
        }
        function format (num) {
            if (parseInt(num) < 10) {
                num = '0' + num;
            }
            return num;
        }
    },
    amount (v) {
        v = Number(v);
        if (v > 9999) {
            return v / 10000 + '';
        }
        return v + '';
    }
};



